import React from 'react';
import { Helmet } from 'react-helmet';
import ReactMarkdown from 'react-markdown';
import gfm from 'remark-gfm';
import rehypeRaw from 'rehype-raw';
import { graphql } from 'gatsby';
import { FaEnvelope, FaFacebookF, FaLinkedinIn, FaTwitter, FaWhatsapp } from 'react-icons/fa';
import Layout from '../layouts';
import SingleArticle from '../components/Banners/SingleArticle';
import SEO from '../components/SearchEngine';
import * as styles from './singlearticleprod.module.scss';

const checkWindow = typeof window !== 'undefined';

const SingleArticleProd = ({ location, data }) => {
  const bannerData = {
    title: data.markdownRemark.frontmatter.title,
    author: data.markdownRemark.frontmatter.author,
    date: data.markdownRemark.frontmatter.date,
    category: data.markdownRemark.frontmatter.category,
    photo_credit: data.markdownRemark.frontmatter.photo_credit,
  };

  // Configuring variables for Social Media Share Links

  const shareTitle = encodeURI(data.markdownRemark.frontmatter.title);

  // Encoded https://theafricaiknow.github.io/
  const currentUrl = 'https%3A%2F%2Ftheafricaiknow.github.io%2F';

  const shareLocationPath = location.pathname.replace('/', '%2F');

  const socials = [
    {
      path: `https://twitter.com/intent/tweet?url=${currentUrl}${shareLocationPath}&text=${shareTitle}&via=sparkplussng`,
      icon: <FaTwitter size={27} />,
      platform: 'Twitter',
    },
    {
      path: `https://www.facebook.com/sharer/sharer.php?u=${currentUrl}${shareLocationPath}&t=${shareTitle}`,
      icon: <FaFacebookF size={27} />,
      platform: 'FaceBook',
    },
    {
      path: `https://www.linkedin.com/sharing/share-offsite/?url=${currentUrl}${shareLocationPath}&title=${shareTitle}`,
      icon: <FaLinkedinIn size={27} />,
      platform: 'LinkedIn',
    },
    {
      path: `whatsapp://send?text=${data.markdownRemark.frontmatter.title}. Read at - https://theafricaiknow.github.io/${location.pathname}`,
      icon: <FaWhatsapp size={27} />,
      platform: 'Whatsapp',
    },
    { path: '#', icon: <FaEnvelope size={27} />, platform: 'Email' },
  ];

  const finalTitle = data.markdownRemark.frontmatter.title.toUpperCase();

  // HANDLE SHARE BUTTON CLICK

  function shareButtonClicked(e, platform) {
    if (platform !== 'Whatsapp' && platform !== 'Email') {
      if (checkWindow) {
        // eslint-disable-next-line no-undef
        window.open(
          e.target.href,
          '',
          'menubar=no,toolbar=no,resizable=yes,scrollbars=yes,height=300,width=600',
        );
        return false;
      }
    }

    if (platform === 'Whatsapp') {
      if (checkWindow) {
        // eslint-disable-next-line no-undef
        window.open(
          e.target.href,
          '_blank',
          'menubar=no,toolbar=no,resizable=yes,scrollbars=yes,height=300,width=600',
        );
        return false;
      }
    }

    if (platform === 'Email') {
      return false;
    }

    return false;
  }

  return (
    <Layout
      bannerHeight='700px'
      bannerStyle={{ backgroundImage: 'none' }}
      dynamicBanner={data.markdownRemark.frontmatter.image_url.childImageSharp.fluid.src}
      bannerChildren={<SingleArticle articleData={bannerData} />}
    >
      <SEO title={finalTitle} />
      <Helmet
        meta={[
          {
            name: `og:image`,
            content: data.markdownRemark.frontmatter.image_url.childImageSharp.fluid.src,
          },
        ]}
      />

      <section className='sections bg-altsuperlight w-100'>
        <div className={`${styles.article_container}`}>
          <div className={`${styles.share_vertical}`}>
            <h2>Share</h2>
            <div className={`${styles.socials}`}>
              {socials.map(({ path, icon, platform }) => {
                if (platform === 'Whatsapp') {
                  return (
                    <a
                      className={`me-3 ${styles.social} ${styles.hideOnBigScreen}`}
                      href={path}
                      onClick={shareButtonClicked(platform)}
                      style={{
                        '--social-button-color': `var(--color-white)`,
                        '--social-button-color-hover': `var(--color-dark-200)`,
                        '--social-button-size': `52px`,
                        '--social-button-margin': `18px`,
                      }}
                      rel='noreferrer'
                      title={`Share to ${platform}`}
                    >
                      {icon}
                    </a>
                  );
                }

                return (
                  <a
                    className={`me-3 ${styles.social}`}
                    href={path}
                    onClick={shareButtonClicked(platform)}
                    style={{
                      '--social-button-color': `var(--color-white)`,
                      '--social-button-color-hover': `var(--color-dark-200)`,
                      '--social-button-size': `52px`,
                      '--social-button-margin': `18px`,
                    }}
                    rel='noreferrer'
                    title={`Share to ${platform}`}
                  >
                    {icon}
                  </a>
                );
              })}
            </div>
          </div>

          <div
            className={`row ${styles.article_text}`}
            dangerouslySetInnerHTML={{ __html: data.markdownRemark.html }}
          />

          {data.markdownRemark.frontmatter.about_author !== '' && (
            <div className={`${styles.about_author}`}>
              <h2>About the author</h2>
              <ReactMarkdown remarkPlugins={[gfm]} rehypePlugins={[rehypeRaw]}>
                {data.markdownRemark.frontmatter.about_author}
              </ReactMarkdown>
            </div>
          )}

          <p className={`${styles.contact_us}`}>
            Contact us at <a href='mailto:editors@theafricaiknow.org'>editors@theafricaiknow.org</a>
          </p>

          <div className={`${styles.share}`}>
            <h2>Share this article</h2>
            <div className='d-flex'>
              {socials.map(({ path, icon, platform }) => {
                if (platform === 'Whatsapp') {
                  return (
                    <a
                      className={`me-3 ${styles.social} ${styles.hideOnBigScreen}`}
                      href={path}
                      onClick={shareButtonClicked(platform)}
                      style={{
                        '--social-button-color': `var(--color-white)`,
                        '--social-button-color-hover': `var(--color-dark-200)`,
                        '--social-button-size': `52px`,
                        '--social-button-margin': `18px`,
                      }}
                      rel='noreferrer'
                      title={`Share to ${platform}`}
                    >
                      {icon}
                    </a>
                  );
                }

                return (
                  <a
                    className={`me-3 ${styles.social}`}
                    href={path}
                    onClick={shareButtonClicked(platform)}
                    style={{
                      '--social-button-color': `var(--color-white)`,
                      '--social-button-color-hover': `var(--color-dark-200)`,
                      '--social-button-size': `52px`,
                      '--social-button-margin': `18px`,
                    }}
                    rel='noreferrer'
                    title={`Share to ${platform}`}
                  >
                    {icon}
                  </a>
                );
              })}
            </div>
          </div>
        </div>
      </section>
    </Layout>
  );
};

export const query = graphql`
  query($slug: String!) {
    markdownRemark(frontmatter: { slug: { eq: $slug } }) {
      html
      frontmatter {
        id
        slug
        title
        excerpt
        author
        about_author
        photo_credit
        category
        image_url {
          childImageSharp {
            fluid {
              src
            }
          }
        }
      }
    }
  }
`;

export default SingleArticleProd;
